import * as THREE from 'three'
import * as TWEEN from '@tweenjs/tween.js'
import OrbitControls from 'threejs-orbit-controls'
import Pulse from './Pulse'

class MyGlobe {
	constructor() {
		this.renderer = new THREE.WebGLRenderer()
		this.scene = new THREE.Scene()
		// this.containerId = containerId || "my-globe"
		this.container = null
		this.camera = {
			object: null, // camera object
			controls: null, // orbitControls object
			fov: 60,
			altitude: 500
		}

		this.globeProps = {
			globeRadius: 200,
			widthSegments: 20,
			heightSegments: 20
		}

		this.globeGroup = new THREE.Group()
		this.circleGroup = new THREE.Group()
		this.visibleResults = []
	}

	init() {
		const _this = this
		this.container = document.getElementById("my-globe")
		const WIDTH = this.container.clientWidth
		const HEIGHT = this.container.clientHeight

		/** Renderer */
		this.renderer.setSize(WIDTH, HEIGHT)

		/** Camera */
		this.camera.object = new THREE.PerspectiveCamera(this.camera.fov, WIDTH / HEIGHT, 1, 10000)
		this.camera.object.position.set(0, 200, this.camera.altitude)
		this.camera.object.lookAt(new THREE.Vector3(0, 0, 0))
		this.camera.controls = new OrbitControls(this.camera.object, this.container)
		this.camera.controls.autoRotate = true
		this.camera.controls.autoRotateSpeed = .5
		// this.camera.controls.enableZoom = false

		/** Scene */
		this.scene.background = new THREE.Color( 0x000 )
		this.scene.add(this.camera.object)
		this.container.appendChild(this.renderer.domElement)


		/** Lighting */
		const ambientLight = new THREE.AmbientLight(0x404040)
		this.scene.add(ambientLight)
		const spotLight = new THREE.PointLight(0xffffff);
		spotLight.position.set(500, 500, 500);
		this.camera.object.add(spotLight);

		/** Objects in scene */
		const loader = new THREE.TextureLoader()
		loader.load( 'https://another-quang.github.io/build/assets/night_2048.jpg', texture => {
			const sphere = new THREE.SphereGeometry(this.globeProps.globeRadius, this.globeProps.widthSegments, this.globeProps.heightSegments)
			const material = new THREE.MeshLambertMaterial({
				map: texture,
				// color: 0xffffff,
				// wireframe: true
			})
			const mesh = new THREE.Mesh(sphere, material)
			this.globeGroup.add(mesh)
		})
		this.scene.add(this.globeGroup)
		this.scene.add(this.circleGroup)
		// this.globeGroup.add(new THREE.AxesHelper(1000))
		// this.globeGroup.add(new THREE.GridHelper(1000, 10, 0x0000ff, 0x808080))

		/** Animation */
		function render () {
			requestAnimationFrame(render)

			// _this.circleGroup.children.forEach(c => {
			// 	const centerDistance = _this.camera.object.position.distanceTo(new THREE.Vector3(0, 0, 0))
			// 	const objectDistance = _this.camera.object.position.distanceTo(c.position)
			// 	if (objectDistance < centerDistance) {
			// 		c.update()
			// 	}
			// })
			TWEEN.update()
			_this.camera.controls.update()
			_this.renderer.render(_this.scene, _this.camera.object)
		}

		render()
	}

	applyStatus(globeStatus) {
		this.camera.controls.autoRotate = globeStatus.spinning
	}

	rotateCamera(selectedResult) {
		if (typeof(selectedResult.geojson) !== 'undefined') {
			const _this = this
			this.camera.controls.autoRotate = false

			const { lat, lng } = selectedResult.geojson
			const target = this.calculate3dCoordinates(lat, lng, this.globeProps.globeRadius)

			const coeff = this.camera.altitude / this.globeProps.globeRadius
			let from = {
				x: this.camera.object.position.x,
				y: this.camera.object.position.y,
				z: this.camera.object.position.z
			}

			const to = {
				x: target.x * coeff,
				y: target.y * coeff,
				z: target.z * coeff
			}

			return new TWEEN.Tween(from)
				.to(to, 3000)
				.easing(TWEEN.Easing.Quartic.Out)
				.onUpdate(() => {
					_this.camera.object.position.copy(from);
					_this.camera.object.lookAt(new THREE.Vector3(0, 0, 0));
				})
				.start()
		}
	}

	updatePhaseResults(phaseResults) {
		var _this = this
		phaseResults.forEach(element => {
			if (typeof(element.geojson) !== 'undefined'){
				_this.addCircle(element)
			}
		})
	}

	addCircle(element) {
		const center = this.calculate3dCoordinates(element.geojson.lat + THREE.Math.randFloat(-2, 2), element.geojson.lng + THREE.Math.randFloat(-2, 2), this.globeProps.globeRadius)
		const medalColor = element.n_RankMedal === 1 ? 0xffff00 : (element.n_RankMedal === 2 ? 0xC0C0C0 : 0xCD7F32)
		const radius = THREE.Math.randFloat(5, 10)
		let pulse = new Pulse(radius, medalColor)
		pulse.name = element.n_PhaseResultID
		pulse.position.copy(center)
		pulse.lookAt(new THREE.Vector3(0, 0, 0))
		this.circleGroup.add(pulse)
	}

	addMarker(element) {
		var _this = this
		const coeff = 1 + Math.random() * .2 //this.camera.altitude / this.globeProps.globeRadius

		const startPoint = _this.calculate3dCoordinates(element.geojson.lat, element.geojson.lng, this.globeProps.globeRadius)
		const endPoint = new THREE.Vector3(startPoint.x * coeff, startPoint.y * coeff, (startPoint.z + Math.random() * 5) * coeff)

		const geometry = new THREE.Geometry()
		geometry.vertices.push(startPoint, endPoint)
		const medalColor = element.n_RankMedal === 1 ? 0xffff00 : (element.n_RankMedal === 2 ? 0xC0C0C0 : 0xCD7F32)
		const lineMaterial = new THREE.LineBasicMaterial({
			color: medalColor
		})

		const line = new THREE.Line(geometry, lineMaterial)
		_this.globeGroup.add(line)

		const circleGeometry = new THREE.SphereGeometry(2, 20, 20)
		// const medalColor = element.n_RankMedal === 1 ? 0xffff00 : (element.n_RankMedal === 2 ? 0xC0C0C0 : 0xCD7F32)
		const circleMaterial = new THREE.MeshPhongMaterial({
			color: medalColor
		})
		const mesh = new THREE.Mesh(circleGeometry, circleMaterial)
		mesh.position.copy(endPoint)
		_this.globeGroup.add(mesh)
	}

	calculate3dCoordinates(lat, lng, radius) {
		const phi   = (90 - parseInt(lat, 10)) * (Math.PI / 180)
		const theta = (parseInt(lng, 10) + 180) * (Math.PI / 180)
		const x = -((radius) * Math.sin(phi) * Math.cos(theta))
		const z = ((radius) * Math.sin(phi) * Math.sin(theta))
		const y = ((radius) * Math.cos(phi))

		return new THREE.Vector3(x, y, z)
	}
}

export default MyGlobe